.chat_container {
    display: flex;
    justify-content: center;
    padding: 30px 0px 0px 0px;
}

.chat {
    width: 600px;
    height: calc(100vh - 90px);
}

.chat p {
    margin: 0;
}

.chat {
    border-radius: 6px;
    position: relative;
}


.message_area {
    height: calc(100vh - 190px);
    border-radius: 9px; 
    border: 1px solid #263238;
    position: relative;
    padding: 30px 30px 30px 30px;
}

.message-container {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

::-webkit-scrollbar {
    display: none;
    width: 0px;
}

.message {
    height: auto;
    margin: 0px 0px 16px 0px; 
    display: flex;
}

.message>div {
    width: auto;
    height: auto;
    min-height: 40px;
    max-width: 300px;
    min-width: 160px;
    background-color: #2C72A1;
    border-radius: 6px;
    color: rgb(25, 25, 25);
    padding: 8px 30px;
    font-size: 16px;
    overflow-wrap: break-word;
    word-break: break-word;
    position: relative;
}

#you {
    justify-content: flex-start;
}



#other {
    justify-content: flex-end;
}

#other div {
    justify-content: flex-end;
    background-color: #468dbc;
}


 

.message-detail>p:nth-child(1) {
    font-size: 12px;
    font-weight: bold;
}

.message-detail>p:nth-child(2) {
    font-size: 10px;
    position: absolute;
    right: 8px;
    bottom: 8px;
}


.send_container {
    position: absolute;
    bottom: 0px;
    display: flex;
    width: 100%;
}

.send_container>input {
    display: inline-block;
    padding: 24px 32px;
    width: 100%;
    border-radius: 0px 0px 0px 9px;
    outline: none;
    border: none;
}

.send_container>button {
    background-color: #2C72A1;
    outline: none;
    border: none;
    font-size: 18px;
    border-radius: 0px 0px 9px 0px;
    color: white;
    width: 90px;
}


@media only screen and (max-width:700px) {
    .chat_container {
        width: 100%;
        margin: 0px; 
        padding:  90px 0px 0px 0px;
    }

    .message_area {
        border:none;
    }


    .chat{
        width: 100%;
        height: calc(100vh - 90px);
    }

}