:root {
    --blue: #0F4C75;
    --blueTransparent: #0f4c7575;
    --blueLight:3282B8;
    --blueExtraLight :#BBE1FA;
    --light: #ffffff;
    --dark: #1B262C;
}

html{
    scroll-behavior: smooth;
}



.navbar {
    position: relative;
    top: 0;

    display: flex;
    justify-content: space-between;
    align-items: center;

    z-index: 1;
    transition: left 0.9s ease;

    background-color: #2C72A1;
    padding: 8px 32px;
}

.items{
    display: flex;
    justify-content: center;
    align-items: center;
}

.items>ul {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin: 0px;
    padding: 0px;
}

li {
    list-style: none;
    transition: all 0.9s ease;
    cursor: pointer;
    padding: 8px 16px;
}


.items>ul>li>a {
    text-decoration: none;
    animation: srink 0.9s ease;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes srink {
    from {
        padding: 0px 30px;
    }

    to {
        padding: 0px 10px;
    }

}

.profileHeader{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    display: none;
    text-decoration: none;
    margin: 16px;
}

.profileHeader>p{
  margin: 0px 0px 0px 0px;
}

.myfollowingHeader{
    text-align: center;
    color: white;
    display: none;
}
 
.myfollowingHeader>a {
    font-size: 13px;
    /* text-decoration: none; */
    color: rgb(149, 149, 149);
    display: inline-block;
    margin: 0px 0px 24px 0px;
}

.header-visible
{
    display: none;
}

.humburger {
    position: absolute;
    right: 30px;
    top: 30px;
    display: none;
    z-index: 9;
}

.humburger-line {
    height: 3px;
    width: 40px;
    background-color: var(--light);
    margin: 5px;
    transition: all 0.3s ease;
}

.humburger-line:nth-child(2) {
    width: 30px;

}

.menu:nth-child(1) {
    transform: rotate(45deg) translate(10px, 10px);
}

.menu:nth-child(2) {
    transform: translateX(-30px);
    opacity: 0;
}

.menu:nth-child(3) {
    transform: rotate(-45deg) translate(2px, -1px);
}

.logo-menu{
    position: absolute;
    top: 30px ;
    left: 30px;
}



@media only screen and (max-width:700px) {
    .profileHeader{
        display: flex;
    }

    .header-visible
    {
        display: inline-block;
    }

    .myfollowingHeader{
        display: flex;
    }

    .navbar {
        position: absolute;
        padding: 35px;
        left: -200vh;
        height: 90vh;
        z-index: 8;
        top: 0px;
        
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
    
    .navbar-menu {
        padding: 35px;
        height: 90vh;
        top: -0px;
        left: 0;
        z-index: 8;
    } 

    .items {
        opacity: 1;
        margin-right: 0;
    }

    .items>ul {
        flex-direction: column;
    }

    .items>ul>li {
        border: none;
        height: 60px;
    }


    .humburger {
        display: block;
    }


}