.yss-1{
    width: 300px;
    margin: 0px auto;
    padding: 128px 0px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: white;
}

.yss-1>svg{
    transform: scale(1.9);
}

.yss-1>p{
  font-size: 24px;
  text-transform: uppercase;
  margin: 16px;
}

.yss-1>input{
    /* background-color: rgba(255, 255, 255, 0); */
    color: #2C72A1;
    border: none;
    outline: none;
    padding: 12px 24px;
    margin: 12px 0px;
    border-radius: 50px;
}

.yss-1>input[type="file"]{
    
    /* display: none; */
    margin: 0px;
    width: 160px;
}

.img_label{
    background-color: rgb(255, 255, 255);
    color: #2C72A1;
    border: none;
    outline: none;
    width: 160px;
    font-size: 14px;
    padding: 12px 24px;
    margin: 12px 0px 0px 0px;
    border-radius: 50px; 
}

.yss-1>input[type="file"]::file-selector-button{
    display: none;
} 



.btn-1{
    background-color: #2C72A1;
    border: none;
    outline: none;
    padding: 8px 16px;
    border-radius: 16px;
    color: white;
    letter-spacing: 1px;
    font-weight: bold;
    margin: 16px;
}

.yss-1>a{
    color: #2C72A1;
    text-decoration: none;

}